<template>
  <div class="home">
    <Header-temp>
      <input type="text" slot="slot1" :placeholder="headData.home.placeholder"/>
      <img class="home-search" :src="headData.home.search" alt="home-search" slot="slot2">
      <img class="home-scan" :src="headData.home.scan" alt="home-scan" slot="slot3">
      <img class="home-chat" :src="headData.home.chat" alt="home-chat" slot="slot4">
    </Header-temp>
    <div class="contents">
      <Post v-for="index of 1" :key="index"
            :title="homeData.mainPage[index-1].title"
            :article="homeData.mainPage[index-1].article"
            :type="homeData.mainPage[index-1].type"
            :from="homeData.mainPage[index-1].from"
            :user="homeData.mainPage[index-1].user"
            :userImg="homeData.mainPage[index-1].userImg"
            :isAds="homeData.mainPage[index-1].isAds"
            :imgSrc="homeData.mainPage[index-1].imgSrc"
            :index="index">
      </Post>
      <ul class="service clearfix">
        <li v-for="item in service">
          <img :src="item.imgSrc" alt="">
          <br>
          <span>{{item.title}}</span>
        </li>
      </ul>
      <Post v-for="index of 9" :key="index"
            :title="homeData.mainPage[index].title"
            :article="homeData.mainPage[index].article"
            :type="homeData.mainPage[index].type"
            :from="homeData.mainPage[index].from"
            :user="homeData.mainPage[index].user"
            :userImg="homeData.mainPage[index].userImg"
            :isAds="homeData.mainPage[index].isAds"
            :imgSrc="homeData.mainPage[index].imgSrc"
            ::index="index">
      </Post>
    </div>
  </div>
</template>
<script>
  import HeaderTemp from '../public/header'
  import headData from '../../assets/headData'
  import homeData from '../../assets/home'
  import Post from '../public/post'
    export default {
        name: "home",
        components:{
          HeaderTemp,
          Post
        },
        data(){
          return{
            headData,
            homeData,
            service:[
              {title:'豆瓣时间',imgSrc:'./static/images/home_ico_03.jpg'},
              {title:'豆瓣日历',imgSrc:'./static/images/home_ico_05.jpg'},
              {title:'豆瓣书店',imgSrc:'./static/images/home_ico_07.jpg'},
              {title:'豆瓣视频',imgSrc:'./static/images/home_ico_09.jpg'},
            ],
            delIndex:0
          }
        },
        props:['index']
    }
</script>
<style scoped>
  .home{
    background-color: #f4f4f4;
  }
  .header{
    background-color: #42bd56;
  }
  .header input{
    position: absolute;
    top: 10px;
    left: 10px;

    height: 30px;
    width: 80%;
    padding: 5px 26px;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    border-radius: 8px;

    color: #bbb;
  }
  .header input::-webkit-input-placeholder{
    color: #ccc;
  }
  .header .home-search{
    position: absolute;
    top: 17px;
    left: 15px;

    width: 16px;
    height: 16px;
  }
  .header .home-scan{
    position: absolute;
    top: 16px;
    right: 72px;

    width: 18px;
    height: 18px;
  }
  .header .home-chat{
    position: absolute;
    top: 15px;
    right: 18px;

    width: 21px;
    height: 21px;
  }
  .contents{
    padding-top: 51px;
    padding-bottom: 51px;
  }
  .contents .service{
    margin-bottom: 15px;
    padding: 14px 6%;
    background-color: white;
  }
  .contents .service li{
    float: left;
    width: 25%;
    font-size: 0;
    text-align:center;
  }
  .contents .service li span{
    font-size: 14px;
  }
  .contents .service li img{
    width: 27px;
    margin-bottom: 10px;
  }
</style>



