<template>
  <div class="status">
    <Header-temp>
      <h2 slot="slot1">{{headData.status.title}}</h2>
      <img class="search" :src="headData.status.search" alt="scan" slot="slot2"/>
      <img class="chat" :src="headData.status.chat" alt="chat" slot="slot3"/>
    </Header-temp>
    <div class="contents">
      <div class="heading">
        <img :src="headData.status.subtitleImg" alt="">
        <h3>{{headData.status.subtitle}}</h3>
        <p>{{headData.status.subSpan}}</p>
      </div>
      <div class="group-item" v-for="item in statusData">
        <img :src="item.chart" alt="">
        <h3>{{item.name}}</h3>
        <b>{{item.attention}}人关注</b>
        <p>{{item.text}}</p>
        <span :style="{backgroundImage:spanImg[0]}">{{item.up}}</span>
        <span :style="{backgroundImage:spanImg[1]}">{{item.step}}</span>
        <button>关注</button>
        <i>&times;</i>
      </div>
    </div>
  </div>
</template>
<script>
  import HeaderTemp from '../public/header'
  import headData from '../../assets/headData'
  import statusData from '../../assets/status'
    export default {
        name: "status",
        components:{
          HeaderTemp
        },
        data(){
          return{
            headData,
            statusData:statusData.arr,
            spanImg:[
              'url(./static/images/group_icon_07.jpg)','url(./static/images/group_icon_09.jpg)'
            ]
          }
        }
    }
</script>
<style scoped>
  .contents{
    padding-top: 51px;
    padding-bottom: 51px;
    background-color: #f2f1ee;
  }
  .contents .heading{
    padding: 35px 0;
    padding-left: 40%;
    position: relative;
  }
  .contents .heading h3{
    font-size: 16px;
    color: #494949;
    padding-bottom: 5px;
  }
  .contents .heading p{
    font-size: 14px;
    color: #9b9b9b;
  }
  .contents .heading img{
    width: 96px;
    position: absolute;
    top: 20px;
    left: 42px;
  }
  .contents .group-item{
    margin-left: 2.5%;
    margin-bottom: 15px;
    padding: 15px 30px 10px 80px;
    width: 95%;
    background-color: white;
    border-radius: 4px;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    box-shadow: 1px 1px 1px 0 rgba(0,0,0,.3);
    position: relative;
  }
  .contents .group-item img{
    position: absolute;
    width: 35px;
    top: 18px;
    left: 25px;
  }
  .contents .group-item h3{
    color: #494949;
  }
  .contents .group-item b{
    display: block;
    padding-bottom: 10px;
    font-size: 12px;
    color: #9b9b9b;
  }
  .contents .group-item p{
    padding-bottom: 10px;
    font-size: 12px;
    color: #9d9d9d;
  }
  .contents .group-item span{
    margin-right: 15px;
    line-height: 14px;
    padding-left: 18px;
    font-size: 12px;
    color: #ccc;
    background-repeat: no-repeat;
    -webkit-background-size: contain;
    background-size: contain;
  }
  .contents .group-item button{
    width: 61px;
    height: 26px;
    -webkit-border-radius: 4px;
    -moz-border-radius: 4px;
    border-radius: 4px;
    background-color: #42bd56;
    color: white;

    position: absolute;
    top: 20px;
    right: 40px;
  }
  .contents .group-item i{
    font-size: 26px;
    color: #ccc;

    position: absolute;
    top: -5px;
    right: 5px;
  }
</style>



