<template>
  <div class="profile">
    <Header-temp>
      <h2 slot="slot1">{{headData.profile.title}}</h2>
      <img class="setting" :src="headData.profile.setting" alt="scan" slot="slot2"/>
    </Header-temp>
    <div class="contents">
      <img :src="panelSrc" alt="">
    </div>
  </div>
</template>
<script>
  import HeaderTemp from '../public/header'
  import headData from '../../assets/headData'
    export default {
        name: "profile",
        components:{
          HeaderTemp
        },
        data(){
          return{
            headData,
            panelSrc:'./static/images/profile_panel.jpg'
          }
        }
    }
</script>
<style scoped>
  .contents{
    padding-top: 51px;
    padding-bottom: 51px;
    font-size: 0;
  }
  .contents img{
    width: 100%;
  }
</style>



