<template>
    <div class="gallery-container">
      <ul>
        <li v-for="(item,index) in data" :key="index" :style="{left:left(index)}">
          <img :src="item.imgSrc" alt="">
          <h3>{{item.title}}</h3>
          <span v-if="item.rating">评分：{{item.rating}}</span>
          <span v-if="item.totalAmt">{{item.totalAmt}}人想看</span>
        </li>
      </ul>
    </div>
</template>
<script>
    export default {
        name: "scroll-gallery",
        props:['data'],
        methods:{
          left:function(index){
            return index*115+'px'
          }
        }
    }
</script>
<style scoped>
  .gallery-container{
    width: 100%;
    overflow-x: scroll;
    -webkit-overflow-x:scroll;
  }
  .gallery-container::-webkit-scrollbar{
    width: 0;
    height: 0;
    background-color: transparent;
  }
  .gallery-container ul{
    height: 196px;
    position: relative;
  }
  .gallery-container ul li{
    position: absolute;
    top: 0;
  }
  .gallery-container ul li img{
    height: 147px;
  }
  .gallery-container ul li h3{
    width: 100px;
    font-size: 14px;
    color: #494949;
    word-break: break-all;
    text-overflow: ellipsis;
    display: -webkit-box; /** 对象作为伸缩盒子模型显示 **/
    -webkit-box-orient: vertical; /** 设置或检索伸缩盒对象的子元素的排列方式 **/
    -webkit-line-clamp: 1; /** 显示的行数 **/
    overflow: hidden;  /** 隐藏超出的内容 **/
  }
  .gallery-container ul li span{
    font-size: 12px;
    color: #9d9d9d;
  }
</style>



