import Vue from 'vue'
import Router from 'vue-router'
import Home from '../components/home/home'

import Subject from '../components/subject/subject'
import Subject_Movie from '../components/subject/subject_movie'
import Subject_TV from '../components/subject/subject_tv'
import Subject_Read from '../components/subject/subject_read'
import Subject_City from '../components/subject/subject_city'
import Subject_Music from '../components/subject/subject_music'

import Status from '../components/status/status'
import Group from '../components/group/group'
import Profile from '../components/profile/profile'

Vue.use(Router);

export default new Router({
  routes:[
    {
      path: '/home',
      name: '',
      component: Home
    },
    {
      path: '/subject',
      name: 'subject',
      component: Subject,
      children:[
        {path:'/subject/movie',name:'movie',component:Subject_Movie},
        {path:'/subject/read',name:'read',component:Subject_TV},
        {path:'/subject/tv',name:'tv',component:Subject_TV},
        {path:'/subject/city',name:'city',component:Subject_TV},
        {path:'/subject/music',name:'music',component:Subject_TV},
      ]
    },
    {
      path: '/status',
      name: '',
      component: Status
    },
    {
      path: '/group',
      name: '',
      component: Group
    },
    {
      path: '/profile',
      name: '',
      component: Profile
    }
  ]
})



