<template>
    <div class="gallery-container">
      <ul>
        <li v-for="(item,index) in data" :key="index" :style="{left:left(index)}">
          <img :src="item.imgSrc" alt="">
        </li>
      </ul>
    </div>
</template>
<script>
    export default {
        name: "scroll-gallery",
        props:['data'],
        methods:{
          left:function(index){
            return index*240+'px'
          }
        }
    }
</script>
<style scoped>
  .gallery-container{
    width: 100%;
    overflow-x: scroll;
    -webkit-overflow-x:scroll;
  }
  .gallery-container::-webkit-scrollbar{
    width: 0;
    height: 0;
    background-color: transparent;
  }
  .gallery-container ul{
    height: 196px;
    position: relative;
  }
  .gallery-container ul li{
    height: 196px;
    position: absolute;
    top: 0;
  }
  .gallery-container ul li img{
    height: 100%;
  }
</style>



