<template>
    <ul class="footer">
      <li v-for="(item,index) in footList" @click="change(index)">
        <img :src='item.srcActive' :alt="item.title" v-if="item.isActive">
        <img :src='item.srcNormal' :alt="item.title" v-if="!item.isActive">
        <br>
        <span>{{item.title}}</span>
      </li>
    </ul>
</template>
<script>
    import footerData from '../../assets/footerData'
    export default {
        name: "footer",
        data(){
          return{
            footList:footerData.footList,
            prevIndex:0
          }
        },
        methods:{
          change(index){
            if(index!=this.prevIndex){
              this.footList[this.prevIndex].isActive=false;
              this.footList[index].isActive=true;
              this.prevIndex=index;
              this.$router.push({path:this.footList[index].linkTo});
            }
          }
        }
    }
</script>
<style>
  .footer{
    width: 100%;
    height: 50px;

    border-top:1px solid #e4e4e4;
    background-color: white;
    position: fixed;
    left: 0;
    bottom: 0;
    z-index: 100;
  }
  .footer li{
    float: left;
    width: 20%;
    font-size: 0;
    text-align:center;
  }
  .footer li img{
    width: 32px;
    height: 32px;
  }
  .footer li span{
    height: 18px;
    line-height: 18px;
    font-size: 14px;
    color: #999;
  }
</style>



