<template>
    <div class="groupItem">
        <img :src="imgSrc" alt="">
        <h4>{{title}}</h4>
        <p>{{des}}</p>
        <span>{{follow}}人</span>
        <i :style="{backgroundImage:unchecked}"></i>
    </div>
</template>
<script>
    export default {
        name: "groups",
        data(){
          return{
            unchecked:'url(./static/images/ic_group_check_anonymous.png)',
            checked:'url(./static/images/ic_group_checked_anonymous.png)'
          }
        },
        props:['imgSrc','title','des','follow']
    }
</script>

<style scoped>
    .groupItem{
      padding: 30px 20% 10px 25%;
      box-sizing: border-box;
      position: relative;
    }
    .groupItem h4{
      font-size: 16px;
      color: #494949;
      font-weight: bolder;
    }
    .groupItem p{
      font-size: 12px;
      color: #9b9b9b;

      word-break: break-all;
      text-overflow: ellipsis;
      display: -webkit-box; /** 对象作为伸缩盒子模型显示 **/
      -webkit-box-orient: vertical; /** 设置或检索伸缩盒对象的子元素的排列方式 **/
      -webkit-line-clamp: 2; /** 显示的行数 **/
      overflow: hidden;  /** 隐藏超出的内容 **/
    }
    .groupItem img{
      width: 52px;

      position: absolute;
      top: 29px;
      left: 23px;
    }
    .groupItem span{
      font-size: 12px;
      color: #9b9b9b;

      position: absolute;
      top: 30px;
      right: 42px;
    }
    .groupItem i{
      display: block;
      width: 28px;
      height: 28px;
      -webkit-background-size: cover;
      background-size: cover;
      background-repeat: no-repeat;

      position: absolute;
      top: 32px;
      right: 0;
    }
</style>



