<template>
  <div>
    <Header-temp>
      <h2 slot="slot1">{{headData.subject.title}}</h2>
      <img class="search" :src="headData.subject.search" alt="scan" slot="slot2"/>
      <img class="chat" :src="headData.subject.chat" alt="chat" slot="slot3"/>
    </Header-temp>
    <div class="contents">
      <ul class="subNav">
        <li v-for="(item,index) in subNav" :class="{active:item.isActive}" @click="change(index)">{{item.name}}</li>
      </ul>
      <router-view></router-view>
    </div>
  </div>
</template>
<script>
  import HeaderTemp from '../public/header'
  import headData from '../../assets/headData'
    export default {
        name: "subject",
        components:{
          HeaderTemp
        },
        data(){
          return{
            headData,
            subNav:[{name:'电影',isActive:true,path:'movie'},
                    {name:'读书',isActive:false,path:'read'},
                    {name:'电视',isActive:false,path:'tv'},
                    {name:'同城',isActive:false,path:'city'},
                    {name:'音乐',isActive:false,path:'music'}],
            prevIndex:0
          }
        },
        methods:{
          change(index){
            if(index!=this.prevIndex){
              this.subNav[this.prevIndex].isActive=false;
              this.subNav[index].isActive=true;
              this.prevIndex=index;
              this.$router.push({path:this.subNav[index].path});
            }
          }
        },
        created(){
          this.$router.push('/subject/movie')
        }
    }
</script>
<style scoped>
  .contents{
    padding-top: 51px;
  }
  .contents .subNav{
    width: 100%;
    text-align: center;
  }
  .contents .subNav li{
    width: 20%;
    height: 40px;
    line-height: 40px;
    font-size: 14px;
    color: #494949;

    border-bottom:2px solid transparent;

    float: left;
  }
  .contents .subNav .active{
    font-size: 16px;
    font-weight: bold;
    border-bottom:2px solid #494949;
  }
</style>



