<template>
  <div class="movie">
      <h3>今日推荐</h3>
      <Gallery :data="todayData"></Gallery>
      <h3>影院热映</h3>
      <Gallery-mk2 :data="newestData"></Gallery-mk2>
      <h3>院线即将上映</h3>
      <Gallery-mk2 :data="comingSoon"></Gallery-mk2>
      <h3>豆瓣热门</h3>
      <Gallery-mk2 :data="highestVote"></Gallery-mk2>
      <h3>你可能感兴趣</h3>
      <img :src="panels" alt="">
  </div>
</template>
<script>
    import Gallery from '../public/scrollGallery'
    import GalleryMk2 from '../public/scrollGalleryMk2'
    import movieData from '../../assets/book_movie'

    export default {
        name: "subject_movie",
        components:{
          Gallery,
          GalleryMk2
        },
        data(){
          return{
            todayData:movieData.today,
            newestData:movieData.newest,
            comingSoon:movieData.comingSoon,
            highestVote:movieData.highestVote,
            panels:'./static/images/book_movie_panels.jpg'
          }
        }
    }
</script>
<style scoped>
  .movie{
    width: 100%;
    padding: 51px 0 50px 5%;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
  }
  .movie h3{
    height: 50px;
    line-height: 50px;
    color: #333;
  }
  .movie img{
    width: 100%;
  }
</style>



