<template>
  <div class="group">
    <Header-temp>
      <h2 slot="slot1">{{headData.group.title}}</h2>
      <img class="search" :src="headData.group.search" alt="scan" slot="slot2"/>
      <img class="chat" :src="headData.group.chat" alt="chat" slot="slot3"/>
    </Header-temp>
    <div class="contents">
      <div class="heading">
        <img class="banner" :src="headData.group.subtitleImg" alt="">
        <h3>{{headData.group.totalGroupCounted}}个有趣的小组</h3>
        <p>立即选择加入吧!</p>
        <span>根据兴趣推荐</span>
        <img class="sub-banner" :src="headData.group.bannerImg" alt="">
      </div>
      <div class="groups">
        <h3><span>{{groupData.movie.catalog}}</span></h3>
        <Group-blocks v-for="(item,index) in groupData.movie.data" :key="index"
                      :imgSrc="item.imgSrc"
                      :title="item.title"
                      :des="item.des"
                      :follow="item.follow">
        </Group-blocks>
        <h3><span>{{groupData.read.catalog}}</span></h3>
        <Group-blocks v-for="(item,index) in groupData.read.data" :key="index"
                      :imgSrc="item.imgSrc"
                      :title="item.title"
                      :des="item.des"
                      :follow="item.follow">
        </Group-blocks>
        <h3><span>{{groupData.music.catalog}}</span></h3>
        <Group-blocks v-for="(item,index) in groupData.music.data" :key="index"
                      :imgSrc="item.imgSrc"
                      :title="item.title"
                      :des="item.des"
                      :follow="item.follow">
        </Group-blocks>
      </div>
    </div>
  </div>
</template>
<script>
  import HeaderTemp from '../public/header'
  import GroupBlocks from '../public/groups'
  import headData from '../../assets/headData'
  import groupData from '../../assets/group'
    export default {
        name: "group",
        components:{
          HeaderTemp,
          GroupBlocks
        },
        data(){
          return{
            headData,
            groupData
          }
        }
    }
</script>
<style scoped>
  .contents{
    padding-top: 51px;
    padding-bottom: 51px;
  }
  .contents .heading{
    font-size: 0;
    text-align: center;

    width: 100%;
    position: relative;
  }
  .contents .heading .banner{
    width: 100%;
  }
  .contents .heading h3{
    color: #494949;
    font-size: 18px;

    position: absolute;
    top: 56px;
    left: 50%;
    -webkit-transform: translateX(-50%);
    -moz-transform: translateX(-50%);
    -ms-transform: translateX(-50%);
    -o-transform: translateX(-50%);
    transform: translateX(-50%);
  }
  .contents .heading p{
    color: #9b9b9b;
    font-size: 16px;

    position: absolute;
    top: 86px;
    left: 50%;
    -webkit-transform: translateX(-50%);
    -moz-transform: translateX(-50%);
    -ms-transform: translateX(-50%);
    -o-transform: translateX(-50%);
    transform: translateX(-50%);
  }
  .contents .heading span{
    color: #9b9b9b;
    font-size: 14px;

    position: absolute;
    left: 15px;
    bottom: 100px;
  }
  .contents .heading .sub-banner{
    width: 90%;
  }

  .contents .groups{
    padding: 0px 5%;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
  }
  .contents .groups h3{
    text-align: center;
    margin-top: 20px;
    border-top: 1px solid #e4e4e4;
    position: relative;
  }
  .contents .groups span{
    display: block;
    width: 100px;
    height: 20px;
    line-height: 20px;
    background-color: white;

    position: absolute;
    left: 50%;
    -webkit-transform: translateX(-50%);
    -moz-transform: translateX(-50%);
    -ms-transform: translateX(-50%);
    -o-transform: translateX(-50%);
    transform: translateX(-50%);
    top: -13px;
  }
</style>



