<template>
    <div class="post">
        <p class="from" v-show="type">{{type}}{{from}}</p>
        <div class="context clearfix">
          <h3 :class="{shrink:imgSrc,black:isAds}">{{title}}</h3>
          <img :src="imgSrc" alt="" class="pic" v-if="imgSrc">
          <p class="article" :class="{shrink:imgSrc}">{{article}}</p>
        </div>
        <div class="user clearfix">
          <img :src="userImg" alt="" class="userImg">
          <span>{{user}}</span>
          <img :src="optionSrc" alt="" class="options" v-if="!isAds">
          <img :src="adSrc" alt="" class="ads" v-if="isAds">
        </div>
    </div>
</template>
<script>
    export default {
        name: "post",
        data(){
          return{
            optionSrc:'./static/images/home_ico_option.jpg',
            adSrc:'./static/images/home_ico_ads.jpg'
          }
        },
        props:['title','article','type','from','user','userImg','isAds','imgSrc','index']
    }
</script>
<style scoped>

    .post{
      margin-bottom: 15px;
      padding: 18px 6%;
      background-color: white;
      color: #9b9b9b;
      font-size: 0;
    }
    .post .from{
      margin-bottom: 10px;
      padding-left: 10px;
      font-size: 12px;
      border-left:1px solid #ccc;
    }
    .post .context{
      margin-bottom: 10px;
    }
    .post h3{
      float: left;
      font-size: 16px;
      line-height: 24px;
    }
    .post .article{
      padding-top: 5px;
      float: left;
      width: 100%;

      font-size: 14px;
      word-break: break-all;
      text-overflow: ellipsis;
      display: -webkit-box; /** 对象作为伸缩盒子模型显示 **/
      -webkit-box-orient: vertical; /** 设置或检索伸缩盒对象的子元素的排列方式 **/
      -webkit-line-clamp: 2; /** 显示的行数 **/
      overflow: hidden;  /** 隐藏超出的内容 **/
    }
    .post .shrink{
      width: 70%;
      -webkit-line-clamp: 3;
    }
    .post .black{
      color: #222;
    }
    .post .pic{
      width: 86px;
      float: right;
      margin-top: 10px;
    }
    .post .userImg{
      margin-right: 6px;
      width: 16px;
      height: 16px;
      /*图片与文字顶部基线对齐*/
      vertical-align: top;
    }
    .post span{
      font-size: 12px;
    }
    .post .options{
      padding-top: 7px;
      width: 16px;
      float: right;
    }
    .post .ads{
      width: 40px;
      float: right;
    }

</style>



