<template>
  <div id="app">
    <router-view></router-view>
    <Foot-nav></Foot-nav>
  </div>
</template>
<script>
  import FootNav from './components/footbar/footer'
export default {
  name: 'app',
  components:{
    FootNav
  },
  created(){
    this.$router.push('/home')
  }
}
</script>
<style>
  *{
    margin: 0;
    padding: 0;
  }
  .clearfix::after{
    content:',';
    font-size: 0;
    width: 0;
    height: 0;
    display: block;
    clear: both;
  }
  ul,ol{
    list-style: none;
  }
  input,textarea,img,button{
    border: none;
    outline: none;
  }
  b{
    font-weight: normal;
  }
  i{
    font-style: normal;
    font-family: Consolas;
  }
  #app {
  }
</style>



